.colorcont {
    width: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.colorlabel {
    font-size: 48px;
}

.colorshow {
    width: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 20px;
    border-radius: 100px;
}

.colorcode {
    font-size: 28px;
    padding: 20px;

}